	module.exports = "<h2 id=overview>Overview</h2> <p>A modal box is a generic rectangular container that can be used to build modals. A modal box can have three sections: header, body, and footer. Header or body is required. If no <code>pf-c-modal-box__header-title</code> is used, <code>aria-label=&quot;[title of modal]&quot;</code> must be provided for <code>.pf-c-modal-box</code>.</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applies to</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>role=&quot;dialog&quot;</code></td> <td><code>.pf-c-modal-box</code></td> <td>Identifies the element that serves as the modal container. <strong>Required</strong></td> </tr> <tr> <td><code>aria-labelledby=&quot;[id value of .pf-c-modal-box__header-title]&quot;</code></td> <td><code>.pf-c-modal-box</code></td> <td>Gives the modal an accessible name by referring to the element that provides the dialog title. <strong>Required when .pf-c-modal-box__header-title is present</strong></td> </tr> <tr> <td><code>aria-label=&quot;[title of modal]&quot;</code></td> <td><code>.pf-c-modal-box</code></td> <td>Gives the modal an accessible name. <strong>Required when .pf-c-modal-box__header-title is <em>not</em> present</strong></td> </tr> <tr> <td><code>aria-describedby=&quot;[id value of applicable content]&quot;</code></td> <td><code>.pf-c-modal-box</code></td> <td>Gives the modal an accessible description by referring to the modal content that describes the primary message or purpose of the dialog. Not used if there is no static text that describes the modal.</td> </tr> <tr> <td><code>aria-modal=&quot;true&quot;</code></td> <td><code>.pf-c-modal-box</code></td> <td>Tells assistive technologies that the windows underneath the current modal are not available for interaction. <strong>Required</strong></td> </tr> <tr> <td><code>aria-label=&quot;Close&quot;</code></td> <td><code>.pf-c-modal-box__close .pf-c-button</code></td> <td>Provides an accessible name for the close button as it uses an icon instead of text. <strong>Required</strong></td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-modal-box</code></td> <td><code>&lt;div&gt;</code></td> <td>Initiates a modal box. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-modal-box__header</code></td> <td><code>&lt;header&gt;</code></td> <td>Initiates a modal box header. A modal box header is <strong>required</strong> if there is no modal box body.</td> </tr> <tr> <td><code>.pf-c-modal-box__header-title</code></td> <td><code>&lt;h1&gt;</code>, <code>&lt;h2&gt;</code>, <code>&lt;h3&gt;</code>, <code>&lt;h4&gt;</code>, <code>&lt;h5&gt;</code>, <code>&lt;h6&gt;</code></td> <td>Initiates a modal box header title.</td> </tr> <tr> <td><code>.pf-c-modal-box__body</code></td> <td><code>&lt;div&gt;</code></td> <td>Initiates a modal box body. A modal box body is <strong>required</strong> if there is no modal box header.</td> </tr> <tr> <td><code>.pf-c-modal-box__footer</code></td> <td><code>&lt;footer&gt;</code></td> <td>Initiates a modal box footer.</td> </tr> <tr> <td><code>.pf-c-modal-box__close</code></td> <td><code>&lt;div&gt;</code></td> <td>Initiates a modal box close button container. The close container can be nested as the first item in the modal box. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-m-lg</code></td> <td><code>.pf-c-modal-box</code></td> <td>Modifies for a modal box width.</td> </tr> </tbody> </table> ";

